import { BrowserRouter, Route, Redirect} from "react-router-dom";
import React from 'react';
import "./style.css"
import "./backend.js";
import {fetchToDoList, isLoggedIn} from "./backend.js";
import { LoginPage } from "./loginPage.js"
import {WorkspacePage} from "./WorkspacePage.js";
import {HomePage} from "./HomePage.js";
import {Navbar, SideNav} from "./reused-components/navigation.js";
import axios from "axios";
//import {PortfolioPage} from "./Portfolio.js";


class PortfolioPage extends React.Component {
  constructor(props) {
    super(props);
    
  }
  render() {
    
    return <div>{fetchToDoList()[0][0].content}
    <button></button>
    </div>
  }
}
class JourneyPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended" }
  }

  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended" }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended" }
      })
    }
  }
  render() {
    if (!isLoggedIn()) {
      return <Redirect to="/journey_prototype/login" />
    }
    return (<div>
      <div style={{ position: "fixed" }}><Navbar pageName="Your Journey" sideNavAnimation={this.changeState} /></div>
          <div style={{ position: "fixed", zIndex: -1 }} className={this.state.isExtended}>
            <div style={{ display: "inline-block", position: "absolute" }}><SideNav isExtended={"extended"} /></div>
            </div>

    </div>);
  }
}


function App() {
  return (
    <BrowserRouter>
      <Route path="/journey_prototype/login" exact component={LoginPage} />
      <Route path="/journey_prototype/" exact component={HomePage} />
      <Route path="/journey_prototype/workspace" exact component={WorkspacePage} />
      <Route path="/journey_prototype/journey" exact component={JourneyPage} />
      <Route path="/journey_prototype/portfolio" exact component={PortfolioPage} />
    </BrowserRouter>
  );
}

export default App;
